import React from 'react';
import { Link } from 'react-router-dom';

// Components

export default class SignUp extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            form : {
                username: '',
                password: '',
                confirmPassword: '',
                firstname: '',
                lastname: '',
                tel: '',
                address: ''
            },
            msg : ""
        }
    }


    render(){
        const { form, msg } = this.state;
        return(
            <React.Fragment>
                <form onSubmit={this.handleSubmit.bind(this)} className="login-form">
                    <div className="form-title">
                        <h4></h4>
                    </div>
                    <ul>
                        <li>
                            <label htmlFor="username">
                                <div className="input-box">
                                    <input type="email" name="username" id="username" value={ form['username'] } onChange={this.handleChange.bind(this)} placeholder=" (E-mail)" autoComplete="off" />
                                </div>
                            </label>
                        </li>
                        <li>
                            <label htmlFor="password">
                                <div className="input-box">
                                    <input type="password" name="password" id="password" value={ form['password'] } onChange={this.handleChange.bind(this)} placeholder=" (8)"/>
                                </div>
                            </label>
                        </li>
                        <li>
                            <label htmlFor="confirmPassword">
                                <div className="input-box">
                                    <input type="password" name="confirmPassword" id="confirmPassword" value={ form['confirmPassword'] } onChange={this.handleChange.bind(this)} placeholder=""/>
                                </div>
                            </label>
                        </li>
                        <li>
                            <label htmlFor="firstname">
                                <div className="input-box">
                                    <input type="text" name="firstname" id="firstname" value={ form['firstname'] } onChange={this.handleChange.bind(this)} placeholder="" autoComplete="off" />
                                </div>
                            </label>
                            <label htmlFor="lastname">
                                <div className="input-box">
                                    <input type="text" name="lastname" id="lastname" value={ form['lastname'] } onChange={this.handleChange.bind(this)} placeholder="" autoComplete="off" />
                                </div>
                            </label>
                        </li>
                        <li>
                            <label htmlFor="tel">
                                <div className="input-box">
                                    <input type="tel" name="tel" id="tel" value={ form['tel'] } onChange={this.handleChange.bind(this)} placeholder="" autoComplete="off"/>
                                </div>
                            </label>
                        </li>
                        <li>
                            <label htmlFor="address">
                                <div className="input-box">
                                    <input type="text" name="address" id="address" value={ form['address'] } onChange={this.handleChange.bind(this)} placeholder="" autoComplete="off"/>
                                </div>
                            </label>
                        </li>
                    </ul>
                    <div className="form-row form-p" data-content="center">
                        <p> kolhunter <Link to=""></Link>  <Link to=""></Link></p>
                    </div>
                    {
                        msg!='' &&
                            <div className="form-row msg" data-content="center">{msg}</div>
                    }
                    <div className="form-row form-p" data-content="center">
                        <button type="submit"></button>
                    </div>
                    <div className="form-row form-p" data-content="center">
                        <Link to="/login" className="signup_link"></Link>
                    </div>
                </form>
            </React.Fragment>
        );
    }

    handleChange = (e) => {
        let form = { ...this.state.form };
        let name = e.target.name;
        let val = e.target.value;
        form = { ...form, [name]: val }

        this.setState({
            form
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        const form = { ...this.state.form };
        console.log( form );
    };
}


